// 
// import VueQuillEditor from 'vue-quill-editor'
// // 
// import 'quill/dist/quill.core.css'
// import 'quill/dist/quill.snow.css'
// import 'quill/dist/quill.bubble.css'

import host_config from './port_config'

var host = host_config;

export default {
	/**
	 * 
	 * @param {Object} Vue vue
	 * @param {Object} optons 
	 */
	install(Vue, optons) {
		// 
		// Vue.use(VueQuillEditor)
		if (optons && optons.host) {
			host = optons.host;
		}

		var redirect_url = "";
		Vue.mixin({
			data() {
				return {}
			},
			methods: {

				/**
				 *  
				 */
				$changeTime(o) {
					for (var k in o) {
						if (k.indexOf('time') !== -1) {
							if (typeof (k) == 'string') {
								var val = o[k];
								if (val || val.indexOf('T') !== -1) {
									var v = new Date(o[k]);
									o[k] = v.toStr('yyyy-MM-dd hh:mm:ss');
								} else if (/\d+/.test(val)) {
									if (o[k].length == 10) {
										var v = new Date(o[k] * 1000);
										o[k] = v.toStr('yyyy-MM-dd hh:mm:ss');
									} else if (o[k].length == 13) {
										var v = new Date(o[k] * 1000);
										o[k] = v.toStr('yyyy-MM-dd hh:mm:ss');
									}
								} else if (typeof (k) == 'number') {
									var v = new Date(o[k]);
									o[k] = v.toStr('yyyy-MM-dd hh:mm:ss');
								}
							}
						}
					}
				},

				$message(content, type = "none") {
					uni.showToast({
						title: content,
						icon: type,
						duration: 2000
					});
				},
				$get_rand(len) {
					var rand = Math.random();
					return Math.ceil(rand * 10 ** len);
				},
				/**
				 * 
				 */
				$get_stamp() {
					return new Date().getTime();
				},
				/**
				 * 
				 * @param {String} url 
				 * @param {String} name 
				 */
				$download(url, name) {
					var anchor = document.createElement('a');
					if (!name) {
						var arr = url.split("/");
						name = arr[arr.length - 1];
					}
					if ('download' in anchor) {
						anchor.href = url.replace("~/", host);
						anchor.setAttribute("download", name);
						anchor.className = "download-js-link";
						anchor.style.display = "none";
						document.body.appendChild(anchor);
						setTimeout(function () {
							anchor.click();
							document.body.removeChild(anchor);
						}, 66);
						return true;
					}
				},
				/**
				 * 
				 * @param {String} url 
				 */
				$nav(url) {
					if (url.indexOf("/") === 0) {
						var _uni = uni;
						uni.switchTab({
							url,
							fail: () => {
								console.log("switchTab", url)
							}
						});
						uni.navigateTo({
							url,
							fail: () => {
								console.log("navigateTo", url)
							}
						})

					} else {
						uni.navigateBack({
							delta: 1
						});
					}
				},

				/**
				 * 
				 * @param {String} url
				 */
				$redirect(url) {
					if (url) {
						redirect_url = url;
					} else {
						return redirect_url;
					}
				},
				/**
				 * @description 
				 * @param {String} text 
				 * @param {String} type 
				 */
				$toast(text, type = 'dark') {
					uni.showToast({
						title: text,
						icon: "none",
						duration: 2000
					});
				},
				/**
				 * 
				 * @param {String} time 
				 * @param {String} format 
				 */
				$toTime(time, format) {
					var s_time = time.toString();
					if (s_time.length == 13) {
						time = time / 1000;
					}
					return time.toTime().toStr(format);
				},
				/**
				 * @description 
				 * @param {Array} arr 
				 * @param {String} key 
				 * @param {Object} value 
				 * @return {Array} 
				 */
				$filter(arr, key, value) {
					var ar = [];
					for (var i = 0; i < arr.length; i++) {
						var o = arr[i];
						if (o[key] === value) {
							ar.push(o);
						}
					}
					return ar;
				},

				/**
				 * @description url
				 * @param {Object} obj 
				 * @param {String} url 
				 * @return {String} url
				 */
				$toUrl(obj, url) {
					var queryStr = "";
					for (var key in obj) {
						var value = obj[key];
						if (typeof (value) === 'number') {
							if (value > 0) {
								queryStr += "&" + key + "=" + obj[key];
							}
						} else if (value) {
							queryStr += "&" + key + "=" + encodeURI(value);
						}
					}
					if (url) {
						if (url.endWith('?') || url.endWith('&')) {
							return url + queryStr.replace('&', '');
						} else if (url.indexOf('?') === -1) {
							return url + queryStr.replace('&', '?');
						} else {
							return url + queryStr;
						}
					} else {
						return queryStr.replace('&', '');
					}
				},
				/**
				 * 
				 * @param {Array} list 
				 */
				$toName(list, value, value_key = 'name', key = 'value') {
					var ret = "";
					for (var i = 0; i < list.length; i++) {
						var o = list[i];
						if (o[key] === value) {
							ret = o[value_key];
						}
					}
					return ret;
				},
				/**
				 * url
				 * @param {String} url 
				 * @return {String} 
				 */
				$fullUrl(url) {
					var url_new = "";
					if (url) {
						if (url.indexOf("~/") === 0) {
							url_new = url.replace('~/', host);
						} else if (url.indexOf("/http://") === 0) {
							url_new = url.replace('/http://', 'http://');
						} else if (url.indexOf("/") === 0) {
							url_new = url.replace('/', host);
						} else {
							url_new = url;
						}
					}
					return url_new;
				},
				/**
				 * url
				 * @param {String} url 
				 * @return {String} 
				 */
				$fullImgUrl(url) {
					if (url) {
						return this.$fullUrl(url)
					} else {
						return "/static/img/logo.png"
					}
				},

				/**
				 * GET
				 * @param {String} url 
				 * @param {Object} body 
				 * @param {Funciton} func await
				 * @return {Object} 
				 */
				async $get(url, body, func) {
					url = url.replace('~/', host);
					if (body) {
						url = this.$toUrl(body, url);
					}
					if (func) {
						uni.request({
							url, // 
							method: "GET",
							header: {
								'x-auth-token': this.$store.state.user.token
							},
							dataType: "json",
							success: function (res) {
								func(res.data);
							},
							error: function (err) {
								func(err);
							}
						});
					} else {
						var [error, res] = await uni.request({
							url, // 
							header: {
								'x-auth-token': this.$store.state.user.token
							},
							dataType: "json",
							data: body
						});
						return res.data;
					}
				},
				/**
				 * POST
				 * @param {String} url 
				 * @param {Object} data 
				 * @param {Funciton} func await
				 * @return {Object} 
				 */
				async $post(url, data, func) {
					url = url.replace('~/', host);
					if (func) {
						uni.request({
							url, // 
							method: "POST",
							header: {
								"accept": "application/json, text/plain, */*",
								'x-auth-token': this.$store.state.user.token || null,
								'Content-Type': 'application/json'
							},
							dataType: "json",
							data,
							success: function (res) {
								func(res.data);
							},
							error: function (err) {
								func(err);
							}
						});
					} else {
						var [error, res] = await uni.request({
							url, // 
							method: "POST",
							header: {
								"accept": "application/json, text/plain, */*",
								'x-auth-token': this.$store.state.user.token || null,
								'Content-Type': 'application/json'
							},
							dataType: "json",
							data
						});
						return res.data;
					}
				},
				/**
				 * 
				 * @param {String} url 
				 * @param {Object} body 
				 * @param {Funciton} func await
				 * @return {Object} 
				 */
				async $upload(url, body, func) {
					url = url.replace('~/', host);
					//  - 
					uni.setHeader('x-auth-token', this.$store.state.user.token);
					//  - 
					uni.setHeader('Content-Type', 'multipart/form-data', ['post']);
					if (func) {
						// , 
						uni.post(url, body).then((res) => {
							func(res.data);
						}).catch((res) => {
							func(res);
						});
					} else {
						// 
						var res = await uni.post(url, body);
						return res.data;
					}
				},

				/**
				 * @description 
				 * @param {Function} func 
				 */
				$get_user(func) {
					var _this = this;

					this.$get('~/api/user/state', null, function (json, status) {
						if (json.result) {
							var user = json.result;
							if (user.obj) {
								user = user.obj;
							}
							_this.$store.commit('user_set', user);

							// 
							_this.$get_auth(user.user_group, () => {
								// 
								if (func) {
									func(user);
								}
							});
						} else if (json.error) {
							// 
							_this.$store.commit('sign_out');
						} else {
							_this.$toast('');
						}
					});
				},

				/**
				 * 
				 * @param {String} user_group 
				 */
				async $get_auth(user_group = "", func) {
					// 
					this.$get("~/api/auth/get_list?", {
						user_group
					}, (json) => {
						// 
						this.$store.commit("set_auth", []);
						uni.db.set("auth", []);
						// 
						if (json.result) {
							var auth = json.result;
							if (auth.list) {
								auth = auth.list;
							}
							// 
							this.$store.commit("set_auth", auth);
							uni.db.set("auth", auth);
							// 
							if (func) {
								func();
							}
						} else if (json.error) {
							console.error(json.error)
						}
					});
				},

				/**
				 * 
				 * @param {String} path 
				 */
				$get_power(path) {
					var auth = this.$store.state.web.auth;
					if (!auth || auth.lenght < 1) {
						auth = uni.db.get("auth");
					}
					var obj;
					if (auth && auth.length > 0) {
						for (var i = 0; i < auth.length; i++) {
							var o = auth[i];
							if (o.path === path) {
								obj = o;
								break;
							}
						}
					}
					return obj;
				},

				/**
				 *  
				 * @param {String} action 
				 */
				$check_action(path, action = "get") {
					// console.log("",path,"",action);
					var o = this.$get_power(path);
					if (!o) {
						return true;
					}
					if (["add", "set", "del", "get"].indexOf(action) !== -1 && o[action] != 0) {
						return o[action];
					} else if (o.option) {
						return o.option[action];
					} else {
						return false;
					}
				},

				/**
				 *  
				 * @param {String} action 
				 */
				$check_exam(path, action = "get") {
					var paths = this.$mp.page.route || this.$route.path
					var path = paths.replaceAll("pages", "");
					var o = this.$get_power(path);
					if (o) {
						var option = JSON.parse(o.option);
						if (option[action])
							return true
					}
					return false;
				},

				/**
				 * 
				 */
				$check_pay() {
					var paths = this.$mp.page.route || this.$route.path
					var path = paths.replaceAll("pages", "");
					var o = this.$get_power(path);
					if (o) {
						var option = JSON.parse(o.option);
						if (option.pay)
							return true
					}
					return false;
				},

				/**
				 * 
				 */
				$check_examine() {
					var paths = this.$mp.page.route || this.$route.path
					var path = paths.replace("view", "table").replace("edit", "table").replaceAll("pages", "")
					var o = this.$get_power(path);
					if (o) {
						var option = JSON.parse(o.option);
						if (option.examine)
							return true
					}
					return false;
				},

				/**
				 * 
				 */
				$check_cart_page(path) {
					var o = this.$get_power(path);
					if (o) {
						var option = JSON.parse(o.option);
						if (option.cart_page)
							return true
					}
					return false;
				},

				/**
				 * 
				 * @param {String} action 
				 * @param {String} field 
				 * @param {String} path 
				 */
				$check_register_field(action, field, path) {
					var o = this.$get_power(path);
					var auth;
					if (o && o[action] != 0 && o[action] != false) {
						auth = o["field_" + action];
					}
					if (auth) {
						return auth.indexOf(field) !== -1;
					}
					return false;
				},

				/**
				 * 
				 * @param {String} action 
				 * @param {String} field 
				 */
				$check_field(action, field) {
					// console.log("",field,"",action);
					var paths = this.$mp.page.route || this.$route.path
					var path = paths.replace(/pages/, "")
					var o = this.$get_power(path);
					var auth;
					if (o && o[action]) {
						auth = o["field_" + action];
					}
					if (auth) {
						return auth.indexOf(field) !== -1;
					}
					return false;
				},
				/**
				 * 
				 * @param {String} action 
				 * @param {String} field 
				 * @param {String} path 
				 */
				$check_index_field(action, field, path) {
					// console.log("",field,"",action);
					// var path = this.$route.path.replace(/\/pages/, "")
					console.log(path)
					var o = this.$get_power(path);
					var auth;
					if (o && o[action]) {
						auth = o["field_" + action];
					}
					if (auth) {
						return auth.indexOf(field) !== -1;
					}
					return false;
				},
				/**
				 * rich-text
				 * @param {Sting} content 
				 */
				$setRichTextImage(content) {
					var new_content = content.replace(/<img([\s\w"-=\/\.:;]+)((?:(height="[^"]+")))/ig, '<img$1')
						.replace(/<img([\s\w"-=\/\.:;]+)((?:(width="[^"]+")))/ig, '<img$1')
						.replace(/<img([\s\w"-=\/\.:;]+)((?:(style="[^"]+")))/ig, '<img$1')
						.replace(/<img([\s\w"-=\/\.:;]+)((?:(alt="[^"]+")))/ig, '<img$1')
						.replace(/<img([\s\w"-=\/\.:;]+)/ig, '<img style="width: 100%;" $1');

					return new_content
				},
				/**
				 * 
				 */
				$chooseFile() {
					return new Promise((resolve, reject) => {
						let platform = uni.getSystemInfoSync().platform
						if (platform == "android") { // 
							uni.chooseVideo({
								sourceType: ['camera', 'album'],
								success: function (res) {
									resolve([{
										path: res.tempFilePath
									}])
								}
							});
						} else {
							let chooseFile = uni.chooseFile;
							if (typeof wx !== 'undefined' &&
								typeof wx.chooseMessageFile === 'function') {
								chooseFile = wx.chooseMessageFile;
							}
							chooseFile({
								count: 1,
								sizeType: ["original", "compressed"], //
								sourceType: ["album"], //
								success: function (res) {
									resolve(res.tempFiles)
								},
								error: function (e) {
									reject(e);
								},
							})
						}
					})
				}
			}
		})
	}
}
